import React from 'react';
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom';

//Import components
import Main from './pages/Main'
import Resume from './pages/Resume'

export default function App() {
  return (
    <BrowserRouter>
      <Switch>
        <Redirect exact from="/" to="/main" />
        <Route path="/main">
          <Main />
        </Route>
        <Route path="/resume">
          <Resume />
        </Route>
      </Switch>
    </BrowserRouter>
  );
}